/*ESTILOS DEL MODAL*/

.modal{
margin-top: 100px;
  display: none;
}

.modal:target{
    -webkit-animation: movimiento 15s infinite linear alternate;
            animation: movimiento 15s infinite linear alternate;
    -webkit-animation: movimiento 9s infinite ease-out alternate;
            animation: movimiento 9s infinite ease-out alternate;
    background-image:url(/PORTAFOLIO-/static/media/papper.b55b61ed.jpg);
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
}

/*PROPIEDADES DE LAS ANIMACIONES*/
@-webkit-keyframes movimiento {
    0% {
        background-position: bottom left;
    }
    
    30% {
        background-position: top right;
    }
    60% {
        background-position: top left;
    }
    100% {
        background-position: bottom right;
    }
 }
@keyframes movimiento {
    0% {
        background-position: bottom left;
    }
    
    30% {
        background-position: top right;
    }
    60% {
        background-position: top left;
    }
    100% {
        background-position: bottom right;
    }
 }

