    box-shadow: 0 0 .2rem #fff,
    0 0 .2rem #fff,
    0 0 2rem #bc13fe,
    0 0 0.8rem #bc13fe,
    0 0 2.8rem #bc13fe,
    inset 0 0 1.3rem #bc13fe; 
    margin-right: 29px;
    background-color:  #c894f8;
    border-radius: 5%;
    width: 25px;
    -webkit-flex-direction: initial;
            flex-direction: initial;
}

.datos-contacto img {
	-webkit-animation: flicker-1 3s linear infinite both;
	        animation: flicker-1 3s linear infinite both;
}

/*PROPIEDADES DE LAS ANIMACIONES*/
@-webkit-keyframes flicker-1 {
    0%,
    100% {
      opacity: 1;
    }
    41.99% {
      opacity: 1;
    }
    42% {
      opacity: 0;
    }
    43% {
      opacity: 0;
    }
    43.01% {
      opacity: 1;
    }
    47.99% {
      opacity: 1;
    }
    48% {
      opacity: 0;
    }
    49% {
      opacity: 0;
    }
    49.01% {
      opacity: 1;
    }
  }
  @keyframes flicker-1 {
    0%,
    100% {
      opacity: 1;
    }
    41.99% {
      opacity: 1;
    }
    42% {
      opacity: 0;
    }
    43% {
      opacity: 0;
    }
    43.01% {
      opacity: 1;
    }
    47.99% {
      opacity: 1;
    }
    48% {
      opacity: 0;
    }
    49% {
      opacity: 0;
    }
    49.01% {
      opacity: 1;
    }
  }

aside.datos-contacto {
    -webkit-justify-content: space-around;
            justify-content: space-around;
}


aside.datos-contacto ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-basis: 70%;
            flex-basis: 70%;
}

/*ESTILOS DEL TEXTO DEL FOOTER*/
footer p.creditos {
    -webkit-flex-direction: row;
            flex-direction: row;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-basis: 85%;
            flex-basis: 85%;
    -webkit-justify-content: center;
            justify-content: center;
    color: #c894f8;
    font-size: 1.2313rem;
    opacity: 0.6;
    color: #fbfbfb;
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    border-radius: 10px;
}

/*RESPONSIVE DE LA PAGINA DE CONTACTOS DESDE LOS 768px hasta 1024px*/
@media only screen and (min-width:768px) and (max-width:1024px)  {
    footer p.creditos {
        -webkit-flex-direction: row;
                flex-direction: row;
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-flex-basis: 40%;
                flex-basis: 40%;
        -webkit-justify-content: center;
                justify-content: center;
        color: #c894f8;
        font-size: 1.0313rem;
        opacity: 0.6;
        color: #fbfbfb;
        -webkit-flex-basis: 100%;
                flex-basis: 100%;
        border-radius: 10px;
    }

    article.datos-contacto ul {
        margin-right: 250px;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: flex-start;
                align-items: flex-start;
    }
}
